import { Header } from "../components/Header";
import { Search } from "../components/Search";
import { Banner } from "../components/Banner";
import { GoodsList } from "../components/GoodsList";
import { Footer } from "../components/Footer";
import { Bottom } from "../components/Bottom";
import axios from 'axios'
export class Home extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            bannerList: [],
            goodsList: {}
        }
    }

    componentDidMount() {
    	document.title="360";
		axios.get('./data/01index.json')
				.then(response => {
//					console.log(response.data);
					let newData = response.data.slidebox.map(goods => {
                    //
                    let obj = {
                    	a_src:goods.href,
                        imgSrc: "./img/"+goods.bg_url
                    }
                    	// 
                    	return obj;
                	})
					//(Home)state.bannerList
	                // state.bannerList(Banner)props   state.bannerList (Banner)props
	                this.setState({
	                    bannerList: newData
	                })
				})
				.catch(error => {
					console.log(error);
					alert('');
				});
		axios.get('./data/01index.json')
				.then(response => {
//					console.log(response.data);
					let newList = response.data.main;
	                // <GoodsList>
	                this.setState({
	                    goodsList : newList
	                })
				})
				.catch(error => {
					console.log(error);
					alert('');
				});
    }
	componentDidUpdate(){
		//
//		document.documentElement.scrollTop= document.documentElement.scrollHeight;
//		console.log(document.documentElement.scrollHeight);
		window.scrollTo(0,0);
	}
    render() {
        return (
            <div>
                <Header text="360" />
                <div className="main">
                    <Search />
                    <Banner list={this.state.bannerList} hasPag={true} />
                    <GoodsList list={this.state.goodsList} />
                	<Bottom/>
                </div>
                <Footer />
            </div>
        )
    }
}


