import {NavLink} from "react-router-dom"

let Item = props => {
    return (
        <li>
            <NavLink to={props.path}>
                <div className={"iconfont "+props.icon}></div>
                <p>{props.text}</p>
            </NavLink>
        </li>
    )
}

export class Footer extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            navList : [
                {text:"",path:"/home",icon:"icon-home"},
                {text:"",path:"/goods",icon:"icon-liebiaomoshi"},
                {text:"",path:"/shopCar",icon:"icon-cart"},
                {text:"",path:"/myShow",icon:"icon-mine"}
            ]
        }
    }

    render(){
        let domList = this.state.navList.map((nav,i)=>{
        	//...
            return <Item key={i} {...nav} />
        })

        return (
            <footer>
                <ul>
                    {domList}
                </ul>
            </footer>
        )
    }
}


